// src/utils/helper/index.ts
import Parser from "rss-parser";
import * as cheerio from "cheerio";

export type FeedItem = {
  title: string;
  link: string;
  pubDate: string;
  guid: string;
  content: string; // ì‹¤ì œ ë³¸ë¬¸ (HTML)
  contentSnippet?: string;
  categories?: string[];
};

const RSS_FEED_URL = "https://blog.google/products/gemini/rss/";

export async function getGeminiProductFeed(): Promise<FeedItem[]> {
  try {
    // 1ï¸âƒ£ RSS ê°€ì ¸ì˜¤ê¸°
    const response = await fetch(RSS_FEED_URL, {
      next: { revalidate: 3600 },
    });
    const xmlText = await response.text();

    // 2ï¸âƒ£ RSS íŒŒì‹±
    const parser = new Parser();
    const feed = await parser.parseString(xmlText);

    // 3ï¸âƒ£ ê° ë§í¬ì— ëŒ€í•´ HTML ë³¸ë¬¸ ê°€ì ¸ì˜¤ê¸°
    const items: FeedItem[] = await Promise.all(
      feed.items.map(async (item) => {
        try {
          const html = await fetch(item.link as string).then((res) => res.text());

          const $ = cheerio.load(html);

          // êµ¬ê¸€ ë¸”ë¡œê·¸ëŠ” ë³´í†µ <article> íƒœê·¸ ì•ˆì— ë³¸ë¬¸ì´ ìˆìŒ
          const article = $("article").html() || $("main").html() || "";

          return {
            title: item.title ?? "",
            link: item.link ?? "",
            pubDate: item.pubDate ?? "",
            guid: item.guid ?? item.link ?? "",
            content: article, // ì›ë³¸ HTML ì „ì²´
            contentSnippet: item.contentSnippet,
            categories: item.categories,
          };
        } catch (err) {
          console.error("Error fetching article:", err);
          return {
            title: item.title ?? "",
            link: item.link ?? "",
            pubDate: item.pubDate ?? "",
            guid: item.guid ?? item.link ?? "",
            content: "",
          };
        }
      })
    );

    return items;
  } catch (error) {
    console.error("Error fetching or parsing Gemini RSS feed:", error);
    return [];
  }
}
